import { useMemo } from 'react';
import { Typography, Box } from '@material-ui/core';
import TextField from '@material-ui/core/TextField';
import Paper from '@material-ui/core/Paper';
import Grid from '@material-ui/core/Grid';
import { isPrime, modInverse } from 'utils/numHelpers';

import useNumberInput from 'diffie-hellman/useNumberInput';
import getFastPowerMod from 'diffie-hellman/getFastPowerMod';
import DisplayFormula from 'diffie-hellman/DisplayFormula';
import FastPowerTable from 'diffie-hellman/FastPowerTable';
import KluczeDisplay from './KluczeDisplay';

function RSAPodpisWeryfikacja() {
	//Wiadomo x
	const [x, setX] = useNumberInput(357);
	const [s, setS] = useNumberInput(1630);
	const [e, setE] = useNumberInput(1001);
	const [d, setD] = useNumberInput(761);
	const [n, setN] = useNumberInput(1739);

	// x^n mod e
	const solutionPow = useMemo(() => getFastPowerMod(n, s, e), [n, s, e]);

	return (
		<>
			<Box my={4}>
				<Paper elevation={3}>
					<Grid container>
						<Grid item xs={12}>
							<KluczeDisplay e={e} n={n} d={d} />
						</Grid>
						<Grid item xs={12}>
							<Box p={2}>
								Bob otrzyma od Alicji wiadomo, ktrej skrt wynosi h={x}, oraz jej podpis cyfrowy
								RSA s={s}. Zweryfikuj przez Boba otrzymany od Alicji podpis cyfrowy.
							</Box>
						</Grid>

						<Grid item xs={4}>
							<Box p={2}>
								<TextField
									label='n'
									onChange={setN}
									value={n}
									type='number'
									helperText='Druga warto obu kluczy'
								/>
							</Box>
						</Grid>

						<Grid item xs={4}>
							<Box p={2}>
								<TextField
									label='e'
									onChange={setE}
									value={e}
									type='number'
									helperText='Pierwsza warto klucza 1 - publicznego'
								/>
							</Box>
						</Grid>

						<Grid item xs={4}>
							<Box p={2}>
								<TextField
									label='d'
									onChange={setD}
									value={d}
									type='number'
									helperText='Pierwsza warto klucza 2 - prywatnego'
								/>
							</Box>
						</Grid>

						<Grid item xs={6}>
							<Box p={2}>
								<TextField
									label='x albo h'
									onChange={setX}
									value={x}
									type='number'
									helperText='Wiadomo lub skrt wiadomoci'
								/>
							</Box>
						</Grid>

						<Grid item xs={6}>
							<Box p={2}>
								<TextField
									label='s'
									onChange={setS}
									value={s}
									type='number'
									helperText='Podpis ktry weryfikujemy'
								/>
							</Box>
						</Grid>

						<Grid item xs={12} justify='center'>
							<Box p={2} align='center'>
								<Typography variant='h3'>Weryfikacja podpisu:</Typography>
								h = <DisplayFormula g={'s'} power={'e'} p={'n'} variant={'body1'} /> ={' '}
								<DisplayFormula g={s} power={e} p={n} variant={'body1'} /> = {solutionPow.result}
							</Box>
							<Box p={2} align='center'>
								Wynik sprawdzenia = {solutionPow.result} = {x} = h
							</Box>
							<Box p={2} pb={3} align='center'>
								Podpis {x === solutionPow.result ? 'jest' : 'nie jest'} poprawny, poniewa warto
								skrtu wysana przez alicj {x === solutionPow.result ? 'jest' : 'nie jest'} taka
								sama jak warto skrtu wiadomoci odebranej przez boba.
							</Box>
						</Grid>

						<Grid container justify='center'>
							<Grid item xs={6}>
								<FastPowerTable stepsObj={solutionPow} pow={d} />
							</Grid>
						</Grid>
					</Grid>
				</Paper>
			</Box>
		</>
	);
}

export default RSAPodpisWeryfikacja;
